ontainer__occurrence-text__icon {\n          width: 16px !important;\n        }\n\n        .log-container__occurrence-text__content {\n          color: #f19737;\n          font-weight: bold;\n          font-size: 16px;\n\n          @media (max-height: 900px) {\n            font-size: 14px;\n          }\n        }\n\n        .log-container__occurrence-text__summary {\n          .log-container__occurrence-text__summary-iterator {\n            display: flex;\n            flex-direction: column;\n            align-items: flex-start;\n\n            .log-container__occurrence-text__summary-iterator-item {\n              display: flex;\n              flex-direction: row;\n              gap: 2px;\n\n              .log-container__occurrence-text__summary-iterator-item__see-more {\n                color: $--color-primary;\n                font-size: 12px;\n                min-width: 66px;\n                cursor: pointer;\n                display: flex;\n                align-items: flex-end;\n                text-decoration: underline;\n                align-self: flex-start;\n                justify-content: flex-end;\n\n                &.visible {\n                  min-width: 114px;\n                }\n              }\n            }\n          }\n        }\n      }\n\n      .log-container__occurrence-about {\n        margin: 6px 10px;\n      }\n    }\n\n    &.normal {\n      padding: 12px;\n    }\n\n    .log-container__occurrence-unknowns {\n      display: flex;\n      flex-direction: column;\n    }\n\n    .log-container__occurrence-text {\n      display: flex;\n      align-self: center;\n      justify-content: center;\n      align-items: center;\n      gap: 6px;\n      text-align: center;\n\n      .log-container__occurrence-text__icon {\n        align-self: flex-start;\n        width: 16px;\n      }\n\n      .log-container__occurrence-text__content {\n        font-size: 16px;\n\n        @media (max-height: 900px) {\n          font-size: 14px;\n        }\n      }\n    }\n\n    .log-container__occurrence-about {\n      .log-container__occurrence-about-time {\n        word-break: keep-all;\n      }\n\n      .log-container__occurrence-about-icon {\n        width: 14px;\n      }\n    }\n\n    .log-container__summary-scheduler {\n      border-right: none;\n      border-bottom: none;\n      border-left: none;\n      margin: 0px;\n      padding: 10px;\n      border-radius: 0px;\n      max-width: 100%;\n      width: 100%;\n    }\n\n    .unkbown-party-resolved {\n      text-align: center;\n      text-decoration: underline;\n    }\n  }\n}\n","/* Colors\n-------------------------- */\n\n$--color-white: #fff !default;\n$--color-black: #424242 !default;\n$--color-gray: #979797 !default;\n$--color-gray-light-1: mix($--color-white, $--color-gray, 10%) !default;\n$--color-light-gray: #f3f4f5 !default;\n$--color-whatsapp: #075E54 !default;\n\n$--pj-color-darker-blue: #0A0022 !default;\n$--pj-color-dark-blue: #280BA0 !default;\n$--pj-color-blue: #0756e4 !default;\n$--pj-color-light-blue: #018EF0 !default;\n$--pj-color-lighter-blue: #30A9FB !default;\n$--pj-color-white-blue: #F0F9FE !default;\n$--pj-color-disabled-blue: #9ad2f9 !default;\n\n$-pg-color-overlay: #19415F !default;\n\n$--pj-color-red: #CC4148 !default;\n$--pj-color-light-red: #EC767C !default;\n$--pj-color-disable-red: #FFF2F3 !default;\n\n$--color-primary: $--pj-color-blue !default;\n$--color-primary-light-1: mix($--color-white, $--color-primary, 10%) !default;\n$--color-primary-light-2: mix($--color-white, $--color-primary, 20%) !default;\n$--color-primary-light-3: mix($--color-white, $--color-primary, 30%) !default;\n$--color-primary-light-4: mix($--color-white, $--color-primary, 40%) !default;\n$--color-primary-light-5: mix($--color-white, $--color-primary, 50%) !default;\n$--color-primary-light-6: mix($--color-white, $--color-primary, 60%) !default;\n$--color-primary-light-7: mix($--color-white, $--color-primary, 70%) !default;\n$--color-primary-light-8: mix($--color-white, $--color-